.view-container {
  font-family: 'Montserrat', sans-serif;
  width: 325px;
  height: 550px;
  background: #fff;
  border-radius: 30px;
  padding: 15px 10px;
  box-shadow: 0 16px 32px 0 rgba(143, 43, 16, 0.16), 0 0px 1px 0 rgba(0, 0, 0, 0.12); }
  .view-container .vote {
    margin: 90px 20px;
    color: #333; }
  .view-container .category {
    color: #ff9f09;
    font-weight: 600;
    margin: 10px 2px; }
  .view-container .question {
    font-size: 29px;
    line-height: 37px;
    margin: 2px 10px 12px 0; }
  .view-container .answer-options {
    margin: 100px 0 30px; }
  .view-container .option {
    margin: 5px 2px;
    font-size: 23px;
    padding: 10px 0;
    display: block;
    background: transparent;
    border: none;
    outline: none;
    cursor: pointer; }
  .view-container .option .answered {
    margin-left: 20px; }
